<script setup>
import { decodeCredential } from "vue3-google-login";
import axios from "axios";
import Swal from "sweetalert2";

const callback = (response) => {
  // This callback will be triggered automatically
  // if one single Google account is already logged in
  const userData = decodeCredential(response.credential);
  const formData = new FormData();
  formData.append("name", userData.name);
  formData.append("email", userData.email);
  formData.append("profile", userData.picture);

  axios({
    method: "post",
    url: "https://adxpaybackend.arimbawadx.my.id/api/new-cus-gakun",
    // url: "http://127.0.0.1:8000/api/new-cus-gakun",
    data: formData,
  })
    .then((response) => {
      window.location.replace("/customer/dashboard");
      Swal.fire({
        icon: response.data.status,
        title: "Berhasil",
        text: response.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
      localStorage.setItem("SessionUser", JSON.stringify(response.data.data));
    })
    .catch((error) => console.log(error));
};
</script>

<template>
  <div v-show="loader" id="preloader"></div>
  <form class="form-signin">
    <img
      class="mb-4"
      src="/img/images/undraw_transfer_money_rywa.svg"
      alt=""
      width="300"
      height="300"
    />
    <div class="form-group">
      <input
        type="password"
        v-model="form.kodeAkses"
        id="password"
        class="form-control form-control-lg rounded-10"
        placeholder="Masukan kode akses"
        required
        autofocus
        autocomplete="off"
      />
    </div>

    <button
      @click="login()"
      type="button"
      class="btn btn-light btn-block btn-lg rounded-10 mb-3"
    >
      Masuk
    </button>
    <!-- <button
      @click="loginWithGooglee()"
      type="button"
      class="btn btn-danger btn-block btn-lg rounded-10"
    >
      Sign With Google
    </button> -->
    <GoogleLogin :callback="callback" />
    <div class="row mt-3">
      <div class="col-12">
        <span class="text-white">Belum punya kode Akses?</span
        ><router-link class="badge badge-light" to="/customer/daftar"
          ><span class="text-primary">Buat Kode</span></router-link
        >
      </div>
    </div>
    <p class="mt-5 mb-3 text-light">
      adxpay&copy; 2021-{{ new Date().getFullYear() }}
    </p>
  </form>
</template>

<script>
export default {
  name: "LoginView",
  props: ["ipAdress", "transaksi", "dataHutang"],

  data() {
    return {
      loader: false,
      form: {
        kodeAkses: "",
      },
    };
  },
  methods: {
    login() {
      if (this.form.kodeAkses == "") {
        Swal.fire({
          icon: "error",
          title: "Gagal",
          text: "Kode Akses Kosong!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        this.loader = true;
        axios
          .post(this.ipAdress + "api/login?username=" + this.form.kodeAkses)
          .then((response) => {
            if (response.data.message == "Unauthorized") {
              Swal.fire({
                icon: "error",
                title: "Gagal",
                text: "Kode Akses Salah",
                showConfirmButton: false,
                timer: 1500,
              });
            } else {
              window.location.replace("/customer/dashboard");
              Swal.fire({
                icon: "success",
                title: "Login Berhasil",
                text: "Selamat Datang " + response.data.data.name,
                showConfirmButton: false,
                timer: 1500,
              });
              localStorage.setItem(
                "SessionUser",
                JSON.stringify(response.data.data)
              );
            }
          })
          .catch((error) => console.log(error))
          .finally(() => (this.loader = false));
      }
    },
  },
};
</script>

<style>
html,
body {
  text-align: center;
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #1583dd;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #ffffff url("@/assets/loader/96x96 (2).gif") no-repeat center
    center;
}
</style>